// ** React Imports
import { Fragment, useState, useEffect } from 'react'

//import UiLoader from '@components/ui-loader' 
import UiLoader from '@components/ui-loader'
import Moment from "moment"
// import AddQuiz from '../add'
import BreadCrumbs from '@components/breadcrumbs'
import { selectThemeColors } from '@utils'
import Select from 'react-select'
// ** Store & Actions
import { getredeem, redeemtoggle} from '../store/actions'
import { useSelector, useDispatch } from 'react-redux'


// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, Row, Col, Label, CustomInput,  Input, Spinner, Button, CardHeader } from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import AppCollapse from '@components/app-collapse'

// ** Table Header
const CustomHeader = () => {
  
  return (
    
     <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75"> 
     
    </div>
  
  )
}

const UsersList = () => {

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.redeem)
  console.log(store.redeem_list)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [status, setStatus] = useState(false)
  const [dataPerPage, setDataPerPage] = useState('')
  console.log(store)
  // ** Get data on mount
  useEffect(() => {    
    dispatch(getredeem(currentPage, dataPerPage))
  }, [currentPage])

  useEffect(() => {
    dispatch(getredeem(currentPage, dataPerPage))
  }, [dataPerPage])

  useEffect(() => {
    if (store.isloader) {      
      <Spinner/>
    } else {
      setListData(store.redeem_list)
    }
  }, [store.isloader])
  console.log(listData)
console.log(store.redeem_list)
  // ** Function in get data on page change
  const handlePagination = (page) => {
    setCurrentPage(page.selected + 1)
  }

  const SwitchClick = (row) => {  
    setStatus(!status)         
    dispatch(redeemtoggle(row, status))   
  }

  const columns = [
        {
          name: '#',
          minWidth: '10px',
          selector: '#',
          cell: (row, pos) =>  `#${store.redeem_page.from + pos}` 
        }, 
        {
          name: 'Prize image',
          minWidth: '10px',
          selector: 'redeem_prizes_assets_url',
          sortable: true,
          cell: (row) => (
            <img src={row.redeem_prizes_assets_url} style={{ height: '50px', width: '50px'}} />
          )
        },
        {
          name: 'prize name',
          minWidth: '20px',
          selector: 'redeem_prizes_name',
          sortable: true,
          cell: (row) => row.redeem_prizes_name
        },
          {
        name: 'prize status',
        minWidth: '20px',
        selector: 'redeem_prizes_status',
        sortable: true,
        cell: (row) => row.redeem_prizes_status
      },
      {
        name: 'Status',
        minWidth: '138px',
        selector: 'id',
        sortable: true,
        cell: (row) => (
          <div className="column-action">
            {            
              (
                <CustomInput 
              className='custom-control-success'
              type='switch'
              id={`success${row.id}`}
              name='success'
              onChange={() => SwitchClick(row.id)}
              inline
              defaultChecked = {row.active !== 0}
            />
              ) 
            }                 
          </div>
        )
      }, 
      {
        name: 'date',
        minWidth: '160px',
        selector: 'created_on',
        sortable: true,
        cell: (row) => { return Moment((row.created_on), 'YYYY-MM-DD').format("DD MMM YYYY HH:MM:SS") }
      }
      ]
  
  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  const submit = () => {}

  useEffect(() => {
    if (searchTerm) {
       const filterList = store.cricket_series.filter((userItem) => {
         
        const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric',  hour: 'numeric', minute: 'numeric', second: 'numeric'}) => {
          if (!value) return value 
          return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))
         
        }       
        const date = formatDate(userItem.date_time)

         const newserch =
         userItem.id.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) || 
         userItem.series_name?.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
         status?.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) ||
         userItem.type.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
         date?.toLowerCase().startsWith(searchTerm.toLowerCase()) 
                
        const includes = 
        userItem.id.toString().toLowerCase().includes(searchTerm.toLowerCase()) || 
        userItem.series_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        status?.toString().toLowerCase().includes(searchTerm.toLowerCase()) ||
        userItem.type.toLowerCase().includes(searchTerm.toLowerCase()) ||
        date?.toLowerCase().includes(searchTerm.toLowerCase()) 
        
     if (newserch) {
         return newserch
       } else if (!newserch && includes) {
         return includes
       } else return null
 
       })
 
       setListData(filterList)
     } else {
       setListData(store.redeem_list)
     }
   }, [searchTerm]) 
console.log(store.redeem_list)
console.log(store.redeem_page)
console.log(listData)
  if (store.isloader) {    
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }
  console.log(store)
  // ** Custom Pagination 
  const CustomPagination = () => {
    const count =  store.redeem_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    // if (store.allContests.length > 0) {
    //   return store.allContests
    // } else
    if (listData?.length === 0 && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  const data = [
    {
      title: <div><Filter size={18} /> Filter</div>,
      content: (
        <>
          <Row>
          <Col className='mb-1' md='6' sm='12'>
            <Label>Status</Label>
              <Select
              menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}               
                classNamePrefix='select'       
                // options={prizestatus}      
                // value={value?.label}                
                // onChange={(e) => serieslist(e)}
                // onKeyDown={(e) => handleKeyPress(e)}
              />             
            </Col>
            <Col className='mb-1' md='6' sm='12'>
              <Label>Fixture</Label>
              <Select
                // isDisabled={store.fixloader}
                // onChange={(e) => fixturelist(e)}
                theme={selectThemeColors}
                // value={refresh ? "" : fid}
                className='react-select'
                classNamePrefix='select'
                isMulti
                // defaultValue={fixture[0]}
                // options={fixture ? fixture : ""}
                placeholder='Select cricket fixture...'
                isClearable={true}
              />
            </Col>
          </Row>         
          <Row>
            <Col className='mb-1 mt-1' xl={6}>
              <Label>Player Name:</Label>
              <Input
                id="search-invoice"
                className="w-100"
                type="text"
                // value={searchName}
                placeholder='Enter player name'
                // onChange={(e) => setSearchName(e.target.value)}
                // onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
            <Col className='mb-1 mt-1' xl={6}>
              <Label>Player Type:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                // options={typeOptions}
                placeholder='search player type...'
                isClearable={true}
                // onChange={(e) => typeHandler(e?.label)}
                // onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
          </Row>
          <Button.Ripple
            color='primary'
            onClick={(e) => submit(e)}
          >submit</Button.Ripple>
        </>
      )
    }
  ]
  
  return (
    <Fragment>
        <BreadCrumbs breadCrumbTitle='Redeem' breadCrumbActive='List' />
        <AppCollapse data={data} type='border' accordion className='mb-1'/>
      <Card>
      <CardHeader>
          <Col xl="12" className="d-flex align-items-center p-0">
            <div className="d-flex align-items-center w-100">
              <Label for="rows-per-page">Show</Label>
              <CustomInput
                className="form-control mx-50"
                type="select"
                id="rows-per-page"
                value={dataPerPage}
                onChange={(e) => setDataPerPage(e.target.value)}
                style={{
                  width: '5rem',
                  padding: '0 0.8rem',
                  backgroundPosition:
                    'calc(100% - 3px) 11px, calc(100% - 20px) 13px, 100% 0'
                }}
              >
                <option value="10">10</option>
                <option value="50">50</option>
                <option value="100">100</option>
              </CustomInput>
              <Label for="rows-per-page">Entries</Label>
            </div>
          </Col>
        </CardHeader>
        <DataTable
          noHeader
          // pagination
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage
              rowsPerPage
              searchTerm={searchTerm}
              handleFilter={handleFilter}
            />
          }
        />
      </Card>
      <CustomPagination />
    </Fragment>
  )
}

export default UsersList
